.BSInput{display: -webkit-flex;
display: -moz-flex;
display: -ms-flex;
display: -o-flex;
display: flex; -ms-align-items: center;
align-items: center; justify-content: space-between;}
.BSInput input{max-width: 80px; margin-left: 10px !important;}
.BSInput .BSInput-me{margin-left: 5px;}

.BS-picker-container{display: -webkit-flex;
display: -moz-flex;
display: -ms-flex;
display: -o-flex;
display: flex; -ms-align-items: center;
align-items: center; justify-content: space-around; -webkit-flex-wrap: wrap;
-moz-flex-wrap: wrap;
-ms-flex-wrap: wrap;
-o-flex-wrap: wrap;
flex-wrap: wrap; margin-top: 10px;}
.BS-picker-container label{display: block;
    margin: 0 0 .28571429rem 0;
    color: rgba(0,0,0,.87);
    font-size: .92857143em;
    font-weight: 700;
    text-transform: none;}
.BS-picker-container > div{flex: 0 1 30%;}

.BSResultText{max-width: 350px; margin: 1em auto !important; text-align: left; }
.BSResultText span{display: table;}